@charset "UTF-8";
* {
  color: black;
  padding: 0;
  margin: 0;
}

body {
  background-color: #F8F8F8;
}

.page {
  display: flex;
  position: relative;
  max-width: 1440px;
  margin: auto;
}
.page-header {
  box-sizing: border-box;
  width: 100vw;
  height: 88px;
  background: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  box-shadow: 0 2px 0 #D9FFF5;
  border-bottom: 2px solid #B8FFEC;
}
.page__left-side-elements {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.page__burger-menu {
  background: #FF3E79;
  border-radius: 20px;
  width: 40px;
  height: 40px;
  margin: 10px;
  padding-top: 5px;
}
.page__logo {
  width: 96px;
  height: 50px;
  margin: 5px;
}
.page__right-side-elements {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.page__repair {
  width: 40px;
  height: 40px;
  background: #FF3E79;
  border-radius: 20px;
  margin-left: 15px;
  margin-right: 15px;
  padding-top: 4px;
}
.page__second-logo-line {
  width: 2px;
  height: 32px;
  background: #EAEAEA;
  border-radius: 1px;
}
.page__checkstatus {
  width: 40px;
  height: 40px;
  background: #FF3E79;
  border-radius: 20px;
  margin-right: 10px;
  padding-top: 4px;
}

.main-section {
  width: 100vw;
  max-width: 1120px;
}

.page__first-logo-line, .page-phone, .page-chat, .page-profile, .main__logo-viewport1120 {
  display: none;
}

button {
  outline: none;
  border: 0;
  background: transparent;
  cursor: pointer;
}
button:hover button:not(.brands-logo-table__more) {
  opacity: 0.6;
  transition-duration: 0.5s;
}
button:hover button:not(.brands-logo-table__less) {
  opacity: 0.6;
  transition-duration: 0.5s;
}

.main {
  top: 89px;
  box-sizing: border-box;
  max-width: 1120px;
  height: auto;
}
.main__header {
  width: 100vw;
  max-width: 1120px;
  height: 70px;
}
.main__text {
  font-family: "TT Lakes";
  width: 304px;
  box-sizing: border-box;
  font-size: 28px;
  line-height: 40px;
  padding-top: 15px;
  letter-spacing: -0.6px;
}
.main__text::before {
  content: url(./static/highlight.svg);
  padding-right: 3%;
  height: 40px;
  vertical-align: middle;
}

.nav {
  box-sizing: border-box;
  width: 100vw;
  max-width: 1120px;
}
.nav__menu {
  max-width: 1120px;
  box-sizing: border-box;
  padding-left: 0px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  overflow-y: scroll;
}
.nav__menu::after {
  content: " ";
  position: absolute;
  height: 70px;
  left: 92.5%;
  right: 0%;
  top: 150px;
  background: linear-gradient(270deg, #F8F8F8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
}
.nav::-webkit-scrollbar {
  width: 0;
}
.nav__item {
  display: inline-block;
  font-family: "TT Lakes Medium";
  letter-spacing: -0.2px;
  flex: 0 0 auto;
  height: 40px;
  box-sizing: border-box;
  margin: 5px;
  border-radius: 6px;
  padding: 6px 15px 5px 15px;
  border: 2px solid #F8F8F8;
}
.nav__item:hover {
  box-shadow: -5px -5px 5px -5px rgba(65, 246, 215, 0.6);
}
.nav__item:active {
  box-sizing: border-box;
  background-color: #FFFFFF;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  padding: 6px 15px;
}
.nav__link {
  font-size: 16px;
  line-height: 24px;
  color: #7E7E82;
  box-sizing: border-box;
}
.nav__link:hover, .nav__link {
  text-decoration: none;
  color: #666666;
}

.about-us {
  box-sizing: border-box;
  width: 320px;
}
.about-us__text {
  display: block;
  font-family: "TT Lakes Regular";
  width: 90vw;
  height: 90px;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21;
  overflow: hidden;
  margin-bottom: 15px;
  padding: 0 16px;
}
.about-us__more {
  margin-top: 24px;
  margin-left: 35px;
  margin-bottom: 24px;
  height: 24px;
  vertical-align: middle;
}
.about-us__more:after {
  content: "Показать все";
  width: 130px;
  height: 24px;
  font-family: "TT Lakes Medium";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  cursor: pointer;
  padding-left: 8px;
}
.about-us__less {
  display: inline-block;
  transform: rotate(180deg);
  margin-top: 24px;
  margin-left: 35px;
  margin-bottom: 24px;
  height: 24px;
  vertical-align: middle;
}
.about-us__less:before {
  display: inline-block;
  content: "Скрыть";
  height: 24px;
  font-family: "TT Lakes Medium";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  cursor: pointer;
  transform: rotate(180deg);
  padding-left: 8px;
}
.about-us-photo {
  display: block;
  max-width: 315px;
  left: 0%;
  right: 0%;
  top: 64.66%;
  bottom: 5.86%;
  margin: 0 auto;
  border-radius: 5px;
}

.show-text {
  height: auto;
}

.hide-text {
  display: none;
}

.overflow-hidden {
  overflow-y: hidden;
}

