    position: fixed;
    left: -500px;
    z-index: 10;
    overflow-y: scroll;
  }

  .aside-menu__communication {
    margin-top: 10px;
  }

  .aside-menu__visible {
    left: 0px;
    transition: left 500ms;
    box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2);
  }

  .aside-menu__unvisible {
    left: -500px;
    transition: left 500ms;
    box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2);
  }

  .body-blur {
    -webkit-filter: blur(7px);
            filter: blur(7px);
    transition-duration: 0.5s;
  }

  .aside-menu__x {
    display: block;
  }
