* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input,
textarea {
  outline: none;
}

button:hover {
  opacity: 0.6;
  transition-duration: 0.5s;
}

.popup-call {
  width: 320px;
  position: absolute;
  right: 0;
}

section a {
  text-decoration: none;
}
section button {
  outline: none;
  border: 0;
  background: transparent;
}
section .popup-call__title {
  font-family: "TT Lakes";
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.6px;
  color: #1B1C21;
  margin: 40px 16px auto 0;
  padding-left: 20px;
  border-left: 3px solid #41F6D7;
  border-radius: 2px;
}
section .popup-call__input {
  width: 288px;
  height: 48px;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 8px;
  margin: 24px 16px 0 16px;
  font-family: "TT Lakes Medium";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC;
  padding: 0 20px;
}
section .popup-call__input:focus {
  border: 1px solid #41F6D7 !important;
}
section ::-webkit-input-placeholder {
  font-family: "TT Lakes Medium";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC;
}
section ::-moz-placeholder {
  font-family: "TT Lakes Medium";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC;
}
section :-ms-input-placeholder {
  font-family: "TT Lakes Medium";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC;
}
section ::placeholder {
  font-family: "TT Lakes Medium";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC;
}
section .popup-call__agreement {
  width: 288px;
  font-family: "TT Lakes Medium";
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
  margin: 16px;
}
section a {
  color: #FF3E79;
}
section .popup-call__close {
  display: block;
  background: #FF3E79;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin-top: 24px;
  margin-left: 16px;
  padding-top: 5px;
}
section .popup-call__send {
